:root {
  --color-primary-light: #e1eead;
  --color-primary-mid: #7c9b39;
  --color-primary-dark: #5c7f23;
  --color-secondary-dark: #714c40;
  --color-secondary-light: #d4986d;
  --fancy-border-style: double;
  --fancy-border-width: 5px;

  /* water.css overrides */
  --background: var(--color-primary-light);
}

html {
  scroll-behavior: smooth;
}

main {
  border-width: var(--fancy-border-width);
  border-style: var(--fancy-border-style);
  border-color: var(--color-secondary-light);
  padding: 15px;
}

main,
main > header {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

main,
main > footer {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

footer {
  height: 50px;
  margin-top:20px;
  margin-left:20px;
  margin-right:20px;
  color: black;
  border: none;
}

footer > a {
  margin:0;
  float:right;
}

footer > p {
  margin:0;
  float:left;
}

header {
  height: 400px;
  background-image: url("/img/skyview.jpg");
}

header > img {
  border-top-left-radius: 20px;
  height: 175px;
  width: 175px;
  float: left;
  margin-right: 20px;
}

header > h1 {
  color: black;
  float: left;
}

dl > dt {
  font-weight: 900;
}

dl > dd {
  margin: 15px;
}

main nav {
  display: flex;
  flex-direction: row;
  column-gap: 5px;
}

main nav a {
  margin-top: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  border-radius: 5px;
  font-weight: 600;
  background-color: var(--color-primary-light);
  text-align: center;
  flex-grow: 1;
}

main nav a:hover {
  outline: thin solid var(--color-secondary-dark);
}

iframe {
  max-width: 100%;
}

figure {
  display: inline-block;
  padding: 5px;
  margin: 5px;

  border: thin solid var(--color-secondary-light);
  border-radius: 5px;
}

figure img {
  display: block;
  margin: auto;
  width:100%;
}

figure figcaption {
  padding: 10px;
  text-align: center;
  background-color: var(--color-primary-dark);
  color: white;
}

ul {
  list-style: none;

  column-count: 3;
  column-width: 120px;
  column-gap: 50px;
  padding: 25px;

  column-rule: thin solid var(--color-secondary-light);
  border: thin solid var(--color-secondary-light);
  border-radius: 10px;
}

ul > li {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  break-inside: avoid-column;
}

/* 
  Single Page Navigation
  See https://css-tricks.com/a-whole-website-in-a-single-html-file/ 
  See also https://john-doe.neocities.org/
*/

article {
  padding-top: 800px;
  margin-top: -800px;
}

article,
article:target ~ #home {
  display: none;
}

article:target,
article#home {
  display: block;
}

/*
 Custom Hover
*/

[aria-label] {
    text-decoration:underline;
    text-decoration-style: dotted;
}

[aria-label]:after {
    background-image: url('https://www.freeiconspng.com/uploads/paper-clip-icon-19.png');
    background-repeat:no-repeat;
    background-size: contain;
    height:30px;
    width:30px;
    content:'';
    position:absolute;
}

[aria-label]:hover:before {
    transform: translate(-50px, -50px);
    background-color:var(--color-primary-dark);
    color:white;
    border-radius:5px;
    padding:5px;
    content:attr(title);
    position:absolute;
}
